"use client"

import React from "react"
import { RadarChart, PolarGrid, PolarAngleAxis, PolarRadiusAxis, Radar, Tooltip } from "recharts"

interface PersonaRadarProps {
  profiles: {
    comprehension: number
    attention: number
    focus: number
    retention: number
    engagement_time: number
    assessment_score: number
  }[]
}

export default function PersonaRadar({ profiles }: PersonaRadarProps) {
  const data = profiles.map((p, idx) => ({
    student: `S${idx + 1}`,
    Comprehension: p.comprehension,
    Attention: p.attention,
    Focus: p.focus,
    Retention: p.retention,
    Engagement: p.engagement_time
  }))

  return (
    <RadarChart outerRadius={90} width={350} height={350} data={data}>
      <PolarGrid />
      <PolarAngleAxis dataKey="student" />
      <PolarRadiusAxis />
      <Radar name="Cognitive Skills" dataKey="Comprehension" stroke="#8884d8" fill="#8884d8" fillOpacity={0.6} />
      <Tooltip />
    </RadarChart>
  )
}
